import AppDatetimePicker from '../../basic-component/app-datetime-picker/app-datetime-picker.vue';
import AppRadio from "../../basic-component/app-radio/app-radio";
import AppRadioGroup from "../../basic-component/app-radio/app-radio-group";
import AppDiyFormCheckboxGroup from "./app-diy-form-checkbox-group";
import AppUploadImage from "../../basic-component/app-upload-image/app-upload-image";
import AppTextarea from "../../basic-component/app-textarea/app-textarea";

export default {
    name: 'app-diy-form',
    components: {AppTextarea, AppUploadImage, AppDiyFormCheckboxGroup, AppRadioGroup, AppRadio, AppDatetimePicker},
    props: {
        sign: {
            default: null,
        },
        datePadding: {
            type: String,
            default: '0 24rpx',
        },
        title: {
            type: String,
            default: null,
        },
        backgroundColor: {
            default: '#ffffff',
        },
        backgroundImage: {
            default: null,
        },
        backgroundPosition: {
            default: 'center',
        },
        backgroundWidth: {
            default: 100,
        },
        backgroundHeight: {
            default: 100,
        },
        backgroundRepeat: {
            default: 'no-repeat',
        },
        marginTop: {
            default: 0,
        },
        marginBottom: {
            default: 0,
        },
        paddingTop: {
            default: 0,
        },
        paddingBottom: {
            default: 0,
        },
        list: {
            type: Array,
            default: [],
        },
        showRequiredIcon: {
            type: Boolean,
            default: false,
        },
        itemPaddingX: {
            default: 24,
        },
        itemMarginY: {
            default: 0,
        },
        itemHeight: {
            type: Number,
            default: 88,
        },
        showItemBorder: {
            default: true,
        },
        labelPosition: {
            default: 'left',
        },
        labelColor: {
            default: '#666666',
        },
        labelTextAlign: {
            default: 'right',
        },
        showInputBorder: {
            type: Boolean,
            default: false,
        },
        inputBackground: {
            default: '#ffffff',
        },
        inputBorderColor: {
            default: '#c0c4cc',
        },
        inputTextColor: {
            default: '#666666',
        },
        inputPlaceholderColor: {
            default: '#bbbbbb',
        },
        inputRadius: {
            default: 0,
        },

        showSubmit: {
            type: Boolean,
            default: false,
        },
        submitUrl: {
            type: String,
            default: null,
        },
        submitBtnText: {
            default: '提交',
        },
        submitBtnHeight: {
            default: 80,
        },
        submitBtnPadding: {
            default: 24,
        },
        submitBtnMargin: {
            default: 40,
        },
        submitBtnRadius: {
            default: 40,
        },
        submitBtnBackground: {
            default: '#ff4544',
        },
        submitBtnBorderColor: {
            default: '#ff4544',
        },
        submitBtnTextColor: {
            default: '#ffffff',
        },
        showScrollBtn: {
            default: false,
        },
        labelFs28: {
            default: false,
        },
        marginColor: {
            default: '#ffffff',
        },
        selectBoxColor: {
            default: '#ff4544',
        },
    },
    data() {
        const newList = [];
        for (let i in this.list) {
            const item = this.list[i];
            // if (typeof item.default === 'undefined') {
            //     item.default = null;
            // }
            if (typeof item.default === 'undefined' || item.default === null || item.default === '') {
                if (item.key === 'text' || item.key === 'textarea' || item.key === 'time' || item.key === 'date') {
                    item.default = '';
                }
                if (item.key === 'radio' || item.key === 'checkbox') {
                    item.default = [];
                }
            }
            if (typeof item.value === 'undefined' || item.value === null || item.value === '') {
                item.value = item.default;
            }
            item.hint = item.hint || '';
            if (item.key === 'radio' || item.key === 'checkbox') {
                if (!item.list) item.list = [];
                for (let j in item.list) {
                    if (!item.list[j].value || item.list[j].value === false || item.list[j].value === 'false') {
                        item.list[j].value = false;
                    } else {
                        item.list[j].value = true;
                    }
                }
            }
            if (item.key === 'img_upload' && (item.img_type === 2 || item.img_type === '2') && !item.value) {
                item.value = ['', ''];
            }
            if(item.key == 'text') {
            }
            newList[i] = item;
        }
        return {
            myList: newList,
            randomString: '',
            validateResult: {
                hasError: false,
                errors: [],
            },
            showAllItems: true, //默认展开全部
        };
    },
    computed: {
        uploadShowImage() {
            return function (item) {
                if (Array.isArray(item.value)) {
                    return item.value;
                }
                if (item.value) {
                    return [item.value];
                }
                return null;
            }
        },
        itemClass() {
            if (this.labelPosition === 'left') {
                return 'label-left dir-left-nowrap cross-top';
            }
            if (this.labelPosition === 'inset') {
                return 'label-inset dir-left-nowrap cross-top';
            }
            if (this.labelPosition === 'top') {
                return 'label-top';
            }
        },
        getDateTimeTextPosition() {
            if (this.labelPosition === 'top') {
                return 'left';
            }
            return 'right';
        },
        getInputPaddingLeft() {
            if (this.labelPosition === 'top') {
                if (this.showInputBorder) {
                    return 24;
                } else {
                    return 0;
                }
            }
            return 24;
        },
    },
    created() {
        this.validate();
        this.outputData();
    },
    methods: {
        textInput() {
            this.outputData();



        },
        datetimeChange() {
            this.outputData();



        },
        checkChange() {
            setTimeout(() => {
                this.outputData();
            }, 10);
        },
        handleImageUpload({sign, imageList}) {
            const index = parseInt(sign);
            if (imageList.length === 1) {
                this.myList[index].value = imageList[0];
            } else if (imageList.length > 0) {
                this.myList[index].value = imageList;
            } else {
                this.myList[index].value = '';
            }
            this.outputData();
        },
        handleUserIdFrontUpload({sign, imageList}) {
            const index = parseInt(sign);
            if (imageList.length > 0) {
                this.myList[index].value[0] = imageList[0];
            } else {
                this.myList[index].value[0] = '';
            }
            this.outputData();
        },
        handleUserIdBackUpload({sign, imageList}) {
            const index = parseInt(sign);
            if (imageList.length > 0) {
                this.myList[index].value[1] = imageList[0];
            } else {
                this.myList[index].value[1] = '';
            }
            this.outputData();
        },
        validate() {
            this.validateResult = {
                hasError: false,
                errors: [],
            };
            for (let i in this.myList) {
                const item = this.myList[i];
                if (item.is_required === 1 || item.is_required === '1') {
                    if ((typeof item.value === 'undefined'
                        || item.value === null
                        || item.value === ''
                        || ['radio', 'checkbox'].indexOf(item.key) !== -1 && !item.value.length)
                    ) {
                        this.validateResult.hasError = true;
                        this.validateResult.errors.push({
                            index: i,
                            msg: `"${item.name}"不能为空。`,
                        });
                        continue;
                    }
                    if (item.img_type && parseInt(item.img_type) === 2) {
                        if (!item.value || !item.value.length) {
                            this.validateResult.hasError = true;
                            this.validateResult.errors.push({
                                index: i,
                                msg: `"${item.name}"不能为空。`,
                            });
                            continue;
                        }
                        let imgErr = false;
                        for (let j in item.value) {
                            if (item.value[j] === null || item.value[j] === '') {
                                imgErr = true;
                                break;
                            }
                        }
                        if (imgErr) {
                            this.validateResult.hasError = true;
                            this.validateResult.errors.push({
                                index: i,
                                msg: `"${item.name}"不能为空。`,
                            });
                            continue;
                        }
                    }
                }
            }
            this.$emit('validate',{
                result: this.validateResult,
                sign: this.sign
            });
        },
        outputData() {
            this.validate();
            for (let i in this.myList) {
                this.myList[i].label = this.myList[i].name;
                this.myList[i].required = this.myList[i].is_required;
            }
            this.$emit('input',{
                data: this.myList,
                sign: this.sign
            });





        },
        submit() {
            this.outputData();
            if (this.validateResult.hasError && this.validateResult.errors) {
                uni.showModal({
                    title: '提示',
                    content: this.validateResult.errors[0].msg,
                    showCancel: false,
                });
                return;
            }
            uni.showLoading({
                mask: true,
                title: '正在提交...',
            });
            this.$request({
                url: this.submitUrl ? this.submitUrl : this.$api.diy.page_store,
                method: 'post',
                data: {
                    form_data: JSON.stringify(this.myList),
                },
            }).then(response => {
                uni.hideLoading();
                if (response.code === 0) {
                    setTimeout(() => {
                        let copyList = this.myList;
                        for (let i in copyList) {
                            copyList[i].value = copyList[i].default;
                        }
                        this.myList = [];
                        setTimeout(() => {
                            this.myList = copyList;
                        }, 0);
                    }, 300);
                    uni.showModal({
                        title: '提示',
                        content: response.msg || '提交成功',
                        showCancel: false,
                    });
                } else {
                    uni.showModal({
                        title: '提示',
                        content: response.msg || '提交失败',
                        showCancel: false,
                    });
                }
            }).catch(() => {
                uni.hideLoading();
            });
        },
    },
}
